import axiosInstance from "@/api/axios/axiosInstance";
import { toast } from "sonner";

// Types
export interface DefaultAssistantTemplate {
  id?: number;
  template_id: string;
  name: string;
  description: string;
  category: string;
  first_message?: string;
  system_message?: string;
  model?: any;
  voice?: any;
  transcriber?: any;
  functions?: any[];
  end_call_message?: string;
  end_call_phrases?: string[];
  metadata?: Record<string, any>;
  background_sound?: string;
  backchannel_enabled?: boolean;
  background_denoising_enabled?: boolean;
  model_output_in_messages_enabled?: boolean;
  transport_configurations?: any;
  artifact_plan?: any;
  message_plan?: any;
  start_speaking_plan?: any;
  stop_speaking_plan?: any;
  monitor_plan?: any;
  credential_ids?: string[];
  server_url?: string;
  server_url_secret?: string;
  analysis_plan?: any;
  max_duration_seconds?: number;
  silence_timeout_seconds?: number;
  response_delay_seconds?: number;
  llm_request_delay_seconds?: number;
  num_words_to_interrupt_assistant?: number;
  max_words_per_spoken_response?: number;
  voice_activity_detection?: any;
  hipaa_enabled?: boolean;
  client_messages?: any;
  server_messages?: any;
  is_active?: boolean;
  is_featured?: boolean;
  sort_order?: number;
  usage_count?: number;
  created_by?: number;
  updated_by?: number;
  created_at?: string;
  updated_at?: string;
}

export interface TemplateCategory {
  category: string;
  count: number;
}

export interface TemplateFilters {
  is_active?: boolean;
  category?: string;
  is_featured?: boolean;
}

// Get all default assistant templates
export async function getAllDefaultAssistantTemplates(filters?: TemplateFilters) {
  try {
    const params = new URLSearchParams();
    
    if (filters?.is_active !== undefined) {
      params.append('is_active', filters.is_active.toString());
    }
    if (filters?.category) {
      params.append('category', filters.category);
    }
    if (filters?.is_featured !== undefined) {
      params.append('is_featured', filters.is_featured.toString());
    }

    const queryString = params.toString();
    const url = `/api/default-assistant-templates${queryString ? `?${queryString}` : ''}`;
    
    const res = await axiosInstance.get(url);
    return res.data;
  } catch (error: any) {
    console.error("Error fetching default assistant templates:", error);
    toast.error("Failed to fetch default assistant templates");
    throw error;
  }
}

// Get default assistant template by ID
export async function getDefaultAssistantTemplateById(templateId: string) {
  try {
    const res = await axiosInstance.get(`/api/default-assistant-templates/${templateId}`);
    return res.data;
  } catch (error: any) {
    console.error("Error fetching default assistant template:", error);
    if (error.response?.status !== 404) {
      toast.error("Failed to fetch default assistant template");
    }
    throw error;
  }
}

// Create new default assistant template
export async function createDefaultAssistantTemplate(templateData: Partial<DefaultAssistantTemplate>) {
  try {
    const res = await axiosInstance.post("/api/default-assistant-templates", templateData);
    toast.success("Default assistant template created successfully");
    return res.data;
  } catch (error: any) {
    console.error("Error creating default assistant template:", error);
    const message = error.response?.data?.message || "Failed to create default assistant template";
    toast.error(message);
    throw error;
  }
}

// Update default assistant template
export async function updateDefaultAssistantTemplate(templateId: string, templateData: Partial<DefaultAssistantTemplate>) {
  try {
    const res = await axiosInstance.put(`/api/default-assistant-templates/${templateId}`, templateData);
    toast.success("Default assistant template updated successfully");
    return res.data;
  } catch (error: any) {
    console.error("Error updating default assistant template:", error);
    const message = error.response?.data?.message || "Failed to update default assistant template";
    toast.error(message);
    throw error;
  }
}

// Delete default assistant template
