// Use template (get template data for assistant creation)
export async function useTemplate(templateId: string) {
  try {
    const res = await axiosInstance.post(`/api/default-assistant-templates/${templateId}/use`);
    return res.data;
  } catch (error: any) {
    console.error("Error using template:", error);
    const message = error.response?.data?.message || "Failed to use template";
    toast.error(message);
    throw error;
  }
}

// Get featured templates (for quick access)
export async function getFeaturedTemplates() {
  return getAllDefaultAssistantTemplates({ is_active: true, is_featured: true });
}

// Get templates by category
export async function getTemplatesByCategory(category: string) {
  return getAllDefaultAssistantTemplates({ is_active: true, category });
}
